import React, { useState, useEffect } from "react";
import { Col, Row } from "react-bootstrap";
import { BellFill } from "react-bootstrap-icons";
import axios from "axios";

const MessageBody = () => {
  const [data, setData] = useState([]);

  const loadData = async () => {
    const response = await axios.get("http://localhost:3030/getallmessages");
    setData(response.data);
  };

  useEffect(() => {
    loadData();
  }, []);

  return (
    <div className="messages">
      {data.map((item) => {
        return (
          <a href="#">
            <Row
              style={{
                marginBottom: "20px",
                borderBottom: "2px solid #f0f5f5",
                paddingBottom: "20px",
              }}
            >
              <Col md="2">
                <div className="notification-icon">
                  <span>
                    <BellFill />
                  </span>
                </div>
              </Col>
              <Col md="8" style={{ textTransform: "capitalize" }}>
                <div className="customer_info" style={{ textAlighn: "center" }}>
                  <span
                    style={{
                      fontSize: "13px",
                      fontWeight: "bold",
                      color: "#676767",
                      textTransform: "uppercase",
                    }}
                  >
                    <span style={{ float: "left" }}>Name:{item.name}</span>{" "}
                    <span style={{ marginLeft: "50px" }}>
                      Phone:{item.phone}
                    </span>{" "}
                    <span style={{ float: "right" }}>Email:{item.email}</span>
                  </span>
                </div>
                {item.message}
              </Col>
              <Col md="2">{item.created_at}</Col>
            </Row>
          </a>
        );
      })}
    </div>
  );
};

export default MessageBody;
